/* side-ad css start */
.side-ad {
  .ad-header {
    display: block;
    background-color: $base-color;
    color: #fff;
    padding: rem(8px) rem(10px);
    text-align: center;
    word-break: break-word; } }
.side-ad + .side-ad {
  margin-top: 30px; }
/* side-ad css end */

/* product-card css start */
.product-card {
  @include border-radius(5px);
  padding: rem(10px);
  background-color: #fff;
  box-shadow: 0 0 5px rgba(#000, 0.15);
  height: 100%;
  &__thumb {
    img {
      width: 100%; } }
  &__content {
    margin-top: rem(5px);
    padding: rem(10px);
    .author {
      font-size: rem(14px); }
    .title {
      a {
        font-size: rem(17px); } } }
  .ratings {
    i {
      color: #F9BF00; }
    span {
      font-size: rem(13px); } }
  .price {
    font-family: $heading-font;
    font-weight: 700;
    font-size: rem(22px);
    color: $heading-color; }
  .details {
    @include d-flex;
    align-items: center;
    .left {
      width: 70%; }
    .right {
      width: 30%;
      text-align: right; } }
  &__footer {
    padding-top: rem(10px);
    margin-top: rem(10px);
    border-top: 1px solid $border-color;
    @include d-flex;
    align-items: center;
    .left {
      width: 60%;
      img {
        max-height: 30px;
        display: inline-flex; }
      .category {
        color: $para-color;
        font-weight: 500;
        font-size: rem(14px);
        &:hover {
          color: $base-color; } } }
    .right {
      width: 40%;
      text-align: right;
      .btn--base {
        font-size: rem(12px); } } } }
/* product-card css end */

/* product details section css start */
.product-details-meta {
  @include d-flex;
  margin: rem(-3px) rem(-15px);
  li {
    font-size: rem(14px);
    display: flex;
    align-items: center;
    margin: rem(3px) rem(15px);
    i {
      font-size: rem(20px);
      margin-right: 5px; } } }
/* product details section css end */

/* featured-product-slider css start */
.featured-product-slider {
  .slick-list {
    margin: rem(-7px); }
  .single-slide {
    margin: 7px; }
  .slick-arrow {
    position: absolute;
    top: -60px;
    width: 35px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f1f1f1;
    color: $para-color;
    @include border-radius(3px);
    &.prev {
      right: 45px; }
    &.next {
      right: 0; } } }
/* featured-product-slider css end */

/* blog section css start */
.blog-card {
  padding: rem(15px);
  background-color: #fff;
  box-shadow: 0 0 5px 0 rgba(#000, 0.15);
  &__thumb {
    max-height: 280px;
    overflow: hidden; }
  &__content {
    padding: 0 rem(10px); }
  .title {
    a {
      font-size: rem(20px); } } }
.post-meta {
  &__tags {
    a {
      font-size: rem(14px);
      color: $para-color;
      &::after {
        content: ','; }
      &:last-child {
        &::after {
          display: none; } }
      &:hover {
        color: $base-color; } } }
  &__date {
    font-size: rem(14px); } }
/* blog section css end */

/* testimoinal section css start */
.testimonial-card {
  background-color: #fff;
  box-shadow: 0 0 5px rgba(#000, 0.15);
  @include border-radius(5px);
  &__content {
    padding: rem(30px); }

  &__client {
    @include d-flex;
    padding: rem(20px) rem(30px);
    border-top: 1px solid $border-color;
    align-items: center;
    .thumb {
      width: 60px;
      height: 60px;
      @include border-radius(50%);
      overflow: hidden;
      img {
        width: inherit;
        height: inherit; } }
    .content {
      width: calc(100% - 60px);
      padding-left: rem(20px); } }
  .ratings {
    i {
      color: #F9BF00; }
    span {
      font-size: rem(14px); } } }
.testimonial-slider {
  .slick-list {
    margin: rem(-7px); }
  .single-slide {
    margin: rem(7px); }
  .slick-dots {
    display: flex;
    justify-content: center;
    margin-top: 15px;
    li {
      margin: 0 4px;
      &.slick-active {
        button {
          background-color: $base-color; } }
      button {
        font-size: 0;
        width: 18px;
        height: 7px;
        background-color: #f1f1f1;
        @include border-radius(999px); } } } }
/* testimoinal section css end */

/* sponsor section css start */
.brand-item {
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    display: inline-block;
    max-height: 90px; } }
.brand-slider {
  .slick-list {
    margin: 0 -7px; }
  .single-slide {
    margin: 0 7px; } }
/* sponsor section css end */




/* blog section css start */
.blog-post {
  padding: rem(15px);
  background-color: #fff;
  box-shadow: 0 0 10px rgba(#000, 0.1);
  &__content {
    margin-top: rem(20px);
    padding: 0 rem(10px) rem(10px) rem(10px);
    .title {
      a {
        font-size: rem(20px); } } } }
/* blog section css end */


/* blog-details-section css start */
.blog-details__thumb {
  position: relative;
  img {
    width: 100%; }
  .post__date {
    position: absolute;
    bottom: -1px;
    right: 0;
    width: 75px;
    text-align: center;
    .date {
      font-size: 30px;
      font-weight: 700;
      color: #ffffff;
      background-color: $base-color;
      padding: 10px 5px;
      width: 100%;
      line-height: 1; }
    .month {
      background-color: #ffffff;
      text-transform: uppercase;
      padding: 4px 5px;
      width: 100%;
      line-height: 1;
      font-size: 18px; } } }
.blog-details__content {
  margin-top: 30px;
  p {
    margin-top: 20px; }
  .blog-details__title {
    font-size: 24px; }
  blockquote {
    margin-top: 30px;
    margin-bottom: 0; } }
blockquote {
  font-size: 18px;
  color: $para-color;
  font-style: italic;
  text-align: center;
  padding: 50px 60px;
  background-color: $base-color-two;
  color: #fff;
  @media (max-width: 575px) {
    padding: 30px 40px;
    font-size: 16px; } }
.blog-details__footer {
  text-align: center;
  padding: 50px 0;
  border-bottom: 1px solid #e5e5e5;
  .caption {
    font-size: 24px;
    margin-bottom: 20px; }
  .social__links {
    justify-content: center;
    @include d-flex;
    li {
      a {
        width: 30px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        color: $heading-color;
        &:hover {
          background-color: $base-color;
          color: #ffffff; } } } } }
.comments-area {
  margin-top: 100px;
  margin-right: 30px;
  @include border-radius(8px);
  .title {
    text-transform: capitalize;
    margin-bottom: 20px;
    font-size: 24px; } }
.comments-list {
  .single-comment {
    display: flex;
    flex-wrap: wrap;
    padding: 20px 0;
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0; }
    &__thumb {
      width: 100px;
      height: 100px;
      overflow: hidden; }
    &__content {
      width: (calc(100% - 100px));
      padding-left: 20px;
      @media (max-width: 480px) {
        @include flexWidth(100%);
        padding-left: 0;
        margin-top: 15px; }
      .name {
        text-transform: capitalize; }
      .date {
        font-size: 14px;
        font-style: italic; }
      p {
        margin-top: 5px; } } } }
.reply-btn {
  width: 28px;
  height: 28px;
  background-color: #f7f5fb;
  color: $base-color;
  text-align: center;
  line-height: 28px;
  font-size: 13px;
  float: right;
  margin-top: -30px;
  @include border-radius(50%);
  &:hover {
    background-color: $base-color;
    color: #ffffff; }
  i {
    @include transform(rotate(180deg)); } }
.comment-form-area {
  margin-top: 70px;
  .title {
    font-size: 24px;
    margin-bottom: 20px; } }
.comment-form {
  input, textarea {
    background-color: #ffffff;
    border: 1px solid #e5e5e5;
    &:focus {
      box-shadow: 0px 3px 18px 2px rgba(46, 25, 204, 0.16);
      border-color: transparent; } } }
/* blog-details-section css end */


/* search section css start */
.search-form {
  position: relative;
  .form--control {
    height: 55px; }
  .search-btn {
    position: absolute;
    top: 5px;
    right: 5px;
    height: calc(100% - 10px);
    width: 150px;
    background-color: $base-color;
    color: #fff;
    @include border-radius(5px);
    @extend %transition;
    i {
      font-size: rem(20px); }
    &:hover {
      background-color: darken($base-color, 5%); } } }
/* search section css end */
