/* sidebar css start */
.sidebar {
  padding-left: 30px;
  @media (max-width: 991px) {
    padding-left: 0;
    margin-top: 50px; }
  .widget + .widget {
    margin-top: 50px;
    @media (max-width: 767px) {
      margin-top: 30px; } }
  .widget {
    padding: 30px;
    border: 1px solid #e5e5e5;
    background-color: #fff;
    .search-form {
      position: relative;
      input {
        width: 100%;
        border: 1px solid #e5e5e5; }
      .search-btn {
        position: absolute;
        color: $base-color;
        background-color: transparent;
        top: 0;
        right: 0;
        font-size: 18px;
        color: $para-color;
        width: 50px;
        height: 100%; } }
    .widget__title {
      position: relative;
      padding-bottom: 8px;
      margin-bottom: 25px;
      &::after {
        position: absolute;
        content: '';
        left: 0;
        bottom: 0;
        width: 20px;
        height: 2px;
        background-color: $base-color; } }
    .map {
      iframe {
        width: 100%; } } }
  .list {
    li {
      &:first-child {
        padding-top: 0; }
      &:last-child {
        padding-bottom: 0;
        border-bottom: none; }
      padding: 10px 0;
      border-bottom: 1px solid #e5e5e5;
      .caption {
        font-weight: 600;
        &::after {
          content: ' :'; } }
      .info {
        float: right; } } }
  .categories__list {
    li {
      &:first-child {
        a {
          padding-top: 0; } }
      &:last-child {
        a {
          padding-bottom: 0;
          border-bottom: none; } }
      a {
        display: block;
        color: $para-color;
        padding: 10px 0;
        border-bottom: 1px solid #e5e5e5;
        span {
          float: right;
          &::before {
            content: '('; }
          &::after {
            content: ')'; } } } } }
  .archive__list {
    li {
      &:first-child {
        a {
          padding-top: 0; } }
      &:last-child {
        a {
          padding-bottom: 0;
          border-bottom: none; } }
      a {
        display: block;
        color: $para-color;
        padding: 10px 0;
        border-bottom: 1px solid #e5e5e5;
        &:hover {
          color: $base-color; }
        span {
          float: right;
          &::before {
            content: '('; }
          &::after {
            content: ')'; } } } } }
  .small-post-list {
    .small-post {
      @include d-flex;
      padding: 20px 0;
      border-bottom: 1px solid #e5e5e5;
      &:first-child {
        padding-top: 0; }
      &:last-child {
        padding-bottom: 0;
        border-bottom: none; }
      &__thumb {
        width: 58px; }
      &__content {
        width: calc(100% - 58px);
        padding-left: 20px;
        .post__title {
          a {
            font-size: 16px;
            font-weight: 600; } } } } }
  .tags {
    margin: -5px -2px;
    a {
      font-size: 16px;
      font-weight: 400;
      color: $para-color;
      padding: 10px 20px;
      border: 1px solid #e5e5e5;
      @include transition(all 0.3s);
      margin: 5px 2px;
      text-transform: capitalize;
      &:hover {
        background-color: $base-color;
        color: #ffffff;
        border-color: $base-color; } } } }


.action-sidebar {
  &.active {
    position: fixed;
    top: 100px;
    z-index: 99;
    width: 360px;
    @media (max-width: 991px) {
      position: static; } }
  .cmn-accordion2 {
    .card + .card {
      margin-top: 10px; }
    .card-header {
      .text {
        font-size: 16px;
        font-weight: 600;
        color: #ffffff; }
      .acc-btn {
        background-color: $base-color;
        border: none;
        position: relative;
        &.collapsed {
          background-color: #ffffff;
          @include transition(all 0.3s);
          .text {
            color: $para-color;
            @include transition(all 0.3s); }
          .plus-icon {
            @include transform(rotate(-90deg));
            color: $para-color; } }
        .plus-icon {
          position: absolute;
          top: 15px;
          right: 22px;
          font-size: 18px;
          color: #ffffff;
          @include transform(rotate(0));
          @include transition(all 0.3s); } } }
    .card-body {
      background-color: #ffffff;
      padding: 20px 30px; } }
  .action__list {
    li + li {
      margin-top: 5px; }
    a {
      color: $para-color; } } }
/* sidebar css end */
