/* hero section css start */
.hero {
  padding-top: rem(200px);
  padding-bottom: rem(200px);
  position: relative;
  z-index: 1;
  @include media(1199px) {
    padding-top: rem(150px);
    padding-bottom: rem(150px); }
  @include media(767px) {
    padding-top: rem(110px);
    padding-bottom: rem(110px); }
  &::before {
    @include positionTwo;
    background-color: $base-color-two;
    opacity: 0.65;
    z-index: -1; }
  &__title {
    font-weight: 900;
    color: #fff;
    font-size: rem(48px);
    @include media(1199px) {
      font-size: rem(42px); }
    @include media(575px) {
      font-size: rem(36px); } }
  &__description {
    color: #fff;
    margin-top: rem(15px); } }
.header-search-form {
  position: relative;
  margin-top: rem(45px);
  z-index: 1;
  &::after {
    position: absolute;
    content: '';
    top: -10px;
    left: -10px;
    width: calc(100% + 20px);
    height: calc(100% + 20px);
    background-color: #fff;
    opacity: 0.25;
    @include border-radius(5px);
    z-index: -1; }
  .form--control {
    height: 65px; }
  &__btn {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 120px;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $base-color;
    color: #fff;
    @include border-radius(5px);
    i {
      font-size: rem(18px);
      margin-right: 5px; } } }
/* hero section css end */

/* inner-page-hero css start */
.inner-page-hero {
  padding-top: 70px;
  padding-bottom: 70px;
  position: relative;
  z-index: 1;
  background-position-y: center;
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $bg-one;
    opacity: 0.85;
    z-index: -1; }
  .page-title {
    font-weight: 600;
    color: #ffffff; }
  .page-list {
    @include d-flex;
    margin-top: 15px;
    li {
      padding: 0 15px;
      position: relative;
      color: rgba(#ffffff, 0.7);
      &:first-child {
        padding-left: 0;
        &::before {
          display: none; } }
      &::before {
        position: absolute;
        content: '/';
        color: #ffffff;
        left: 0px; }
      a {
        color: #ffffff; } } } }
/* inner-page-hero css end */
