/* footer section css start */
.footer {
  background-color: $base-color-two;
  &__top {
    padding-top: rem(80px);
    padding-bottom: rem(80px); }
  &__bottom {
    position: relative;
    padding: rem(15px) 0;
    z-index: 1;
    &::after {
      @include positionTwo;
      background-color: darken($base-color-two, 3%);
      z-index: -1; } } }
.footer-info-area {
  padding-bottom: rem(25px);
  border-bottom: 1px solid rgba(#fff, 0.15);
  margin-bottom: rem(40px); }
.footer-info-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  &::after {
    position: absolute;
    content: '';
    top: 50%;
    right: 0;
    width: 1px;
    height: 50px;
    margin-top: -25px;
    background-color: rgba(#fff, 0.15); }
  &:last-child {
    &::after {
      display: none; } }
  .footer-info-number {
    color: $base-color;
    font-weight: 700;
    font-size: rem(24px); }
  .caption {
    color: #fff; } }
.footer-widget {
  &__title {
    font-size: rem(20px);
    color: #fff;
    margin-bottom: rem(20px);
    position: relative;
    padding-bottom: rem(5px);
    &::after {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 35px;
      height: 2px;
      background-color: $base-color; } } }

.footer-menu-list {
  li {
    &:first-child {
      a {
        padding-top: 0; } }
    &:last-child {
      a {
        padding-bottom: 0;
        border-bottom: none; } }
    a {
      padding: rem(10px) 0;
      display: block;
      color: #fff;
      border-bottom: 1px dashed rgba(#fff, 0.15);
      font-size: rem(14px);
      &:hover {
        color: $base-color; } } } }
.footer-social-list {
  li {
    &:first-child {
      a {
        padding-top: 0; } }
    &:last-child {
      a {
        padding-bottom: 0;
        border-bottom: none; } }
    &.facebook {
      a {
        i {
          background-color: #3b5998; } } }

    &.twitter {
      a {
        i {
          background-color: #00acee; } } }
    &.linkedin {
      a {
        i {
          background-color: #0e76a8; } } }
    &.instagram {
      a {
        i {
          background-color: #cd486b; } } }
    &.pinterest {
      a {
        i {
          background-color: #E60023; } } }
    a {
      padding: rem(10px) 0;
      display: flex;
      align-items: center;
      color: #fff;
      border-bottom: 1px dashed rgba(#fff, 0.15);
      font-size: rem(14px);
      &:hover {
        color: $base-color; }
      i {
        width: 26px;
        height: 26px;
        background-color: $base-color;
        color: #fff;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        @include border-radius(3px); }
      span {
        width: calc(100% - 26px);
        padding-left: rem(15px); } } } }

/* footer section css end */
